import React, { useState } from 'react'
import * as S from './style'

const MailingListForm = () => {
  const [onFocus, setOnFocus] = useState(false)

  return (
    <S.Form onFocus={() => setOnFocus(true)} onBlur={() => setOnFocus(false)}>
      <S.Input type="email" placeholder="Enter your email" required />
      <S.Submit type="submit" show={onFocus}>
        Submit
      </S.Submit>
    </S.Form>
  )
}

export default MailingListForm
